.week-goods {
    display: flex;
    flex-direction: column;
}

.week-goods__title {
    margin: 0;
    padding: 0;
    margin-top: 28px;
    
    font-size: 25px;
    line-height: 30px;
    font-weight: 700;
    color: #000000;
}

.week-goods__mark {
    margin: 0;
    padding: 0;
    margin-top: 4px;

    font-size: 17px;
    line-height: 20px;
    font-weight: 700;
    text-transform: uppercase;
    color: #63d1bb;
}

.week-goods__description {
    margin: 0;
    padding: 0;
    margin-top: 24px;

    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    color: #444444;
}

.week-goods__table {
    margin: 0;
    padding: 0;
    margin-top: 24px;
    min-height: 120px;

    font-size: 15px;
    line-height: 20px;
    font-weight: 400;
    color: #444444;
}

.week-goods__table-row {
    display: block;
    box-sizing: border-box;
    min-height: 40px;
    min-width: 260px;
    margin-left: -1px;
    padding-top: 10px;
    padding-bottom: 4px;
    

    border-bottom: 1px solid #ececec;

    &:first-child {
        min-height: 30px;
        padding: 0;
    }
}

.week-goods__table-cell {
    display: inline-block;
    margin: 0;
    padding: 0;
    min-width: 92px;
}

.week-goods__image {
    display: block;
    margin-top: 22px;
}

.week-goods__price {
    font-size: 17px;
    line-height: 24px;
    font-weight: 700;

    text-align: center;
    margin-top: 22px;
    margin-bottom: 28px;

    color: #444444;
}

.week-goods__button {
    height: 50px;
    padding-top: 13px;
}

@media (min-width: 768px) {
    .week-goods {
        display: block; 
        position: relative; 
        margin-bottom: 52px; 
    }

    .week-goods__title {
        display: inline-block;
        width: 470px;
        margin-top: 45px;
        margin-right: 95px;
        font-size: 43px;
        line-height: 50px; 
    }

    .week-goods__mark {
        display: inline-block;  
        width: 70px;
        height: 35px; 
        margin-top: 0;
        margin-left: 0;
        line-height: 17px;
        text-align: center;
    }

    .week-goods__description {
        font-size: 17px;
        line-height: 30px;
        width: 282px;
        margin-top: 49px;
        padding-right: 52px;
    }

    .week-goods__table {
        margin-top: 41px;
    }

    .week-goods__image {
        position: absolute;
        right: 0;
        bottom: 0;
    }

    .week-goods__price {
        font-size: 20px;
        line-height: 24px;
        width: 284px; 
        margin-top: 51px; 
        margin-bottom: 35px; 
    }

    .week-goods__button {
        width: 292px; 
        margin-left: -3px;
    }
}

@media (min-width: 1050px) {
    .week-goods {
        margin-bottom: 80px; 
    }

    .week-goods__title {
        width: 700px;
        font-size: 53px;
        line-height: 57px;

        margin-right: 0;
        margin-left: 221px; 
        margin-top: 124px; 
    }

    .week-goods__mark {
        position: absolute; 
        top: 136px; 
        left: 96px;   
    }

    .week-goods__description {
        margin-right: 0;
        margin-left: 226px;   
    }

    .week-goods__table {
        margin-left: 226px;
        margin-top: 47px;    
    }

    .week-goods__table-row {
        min-width: 285px;
        padding-top: 5px;  

        &:first-child {
            min-height: 40px; 
            padding-top: 5px; 
            padding-bottom: 5px;
        }
    }

    .week-goods__price {
        margin-top: 106px; 
        margin-left: 229px;  
    }

    .week-goods__button {
        margin-left: 225px;  
    }
}
